@import '../../../app/styles/variables';
@import '../../../app/styles/typography';




.button {
    cursor: pointer;
    width: 110px;
    height: 35px;
    margin-right: 0%;
    border-radius: 3px;
    font-weight: 600;
    font-size: 13px;
    letter-spacing: 1px;
    
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    p.title {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        width: 100%; // Заполняет полностью ширину .abadge
    }
}


