// }


.small {
    padding: 8px;
    height: 14px;

    // width: 96.6354px; // Примерный размер для маленького бэйджа
}

.medium {
    padding: 11px;
    height: 27px;
    // width: 200px; // Примерный размер для среднего бэйджа
    width: 120px !important;
}

.big {
    padding: 15px;
    height: 34px;
    width: 200px; // Примерный размер для большого бэйджа
}

$colors: (
    'green': $accountant,
    'blue': $btx-blue,
    'fiolet': $company-pro,
    'orange': $budget-big,
    'light': $btx-card-bckgrnd,
    'dark': $base-text,
    'april': $link-color,
    'warning': $btn-color-danger,
    'danger': $btn-color-error,
    'success': $btx-green
);

// Генерация стилей в зависимости от цвета и активности
@each $color-name, $color-value in $colors {
    .button--#{$color-name} {
        border: 1px solid $color-value !important;

        &.active {
            background-color: $color-value !important;
            color: white; // Предполагаем, что текст белый на активном фоне
        }

        &.inactive {
            background-color: transparent !important;
            color: $color-value !important; // Цвет текста совпадает с цветом границы
        }

        &.withIcone {
            padding-right: 1.8px !important;
        }



    }
}

.title {

    @extend .text__small__bold;
    margin: 0;
    display: inline-block;
}

.iconeDone {
    background-color: none !important;
}